const Readable = require("stream").Readable;
const StateProcessingHeaders = require("./HttpRequestStateProcessingHeaders");
const StateProcessingBody = require("./HttpRequestStateProcessingBody");

class HttpRequest extends Readable {
  constructor(socket) {
    super();
    this._state = new StateProcessingHeaders(this);
    this._socket = socket;
    this._socket.on("data", data => {
      this.onData(data);
    });
    this._headInfo;
  }

  _read() {}

  onData(data) {
    this._state.onData(data);
  }

  _applyState(newState) {
    if (this._state) {
      this._state.exit();
    }

    this._state = newState;
    this._state.entry();
  }

  processHeaders() {}

  get socket() {
    return this._socket;
  }
}

module.exports = HttpRequest;
